
@mixin clearfix() {
  &:before,
  &:after {
    content: " "; /* 1 */
    display: table; /* 2 */
  }
  &:after {
    clear: both;
  }
}

// Webkit-style focus
@mixin tab-focus() {
  // Default
  outline: thin dotted #000;
  // Webkit
  outline: 5px auto -webkit-focus-ring-color;
  outline-offset: -2px;
}

// Center-align a block level element
@mixin center-block() {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

// Sizing shortcuts
@mixin size($width, $height) {
  width: $width;
  height: $height;
}
@mixin square($size) {
  @include size($size, $size);
}

@mixin flexbox(){
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
}

@mixin flex(){
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
}

@mixin flex-inline(){
    display: -webkit-inline-box;
    display: -webkit-inline-flex;
    display: -ms-inline-flexbox;
    display: inline-flex;
}

@mixin align-items($argument){
    -webkit-box-align: $argument;
    -webkit-align-items: $argument;
    -ms-flex-align: $argument;
    align-items: $argument;
}

@mixin flex-column{
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
}

@mixin flex-row{
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
  -ms-flex-direction: row;
  flex-direction: row;
}


// Placeholder text
@mixin placeholder($color, $opacity) {
    &::-moz-placeholder           {
        color: $color;   // Firefox
		opacity: $opacity;
		font-weight: 400;
		font-family: $force-mac-font-family;
    } // See https://github.com/twbs/bootstrap/pull/11526
    &:-ms-input-placeholder       {
        color: $color;
		opacity: $opacity;
		font-weight: 400;
		font-family: $force-mac-font-family;
    } // Internet Explorer 10+
    &::-webkit-input-placeholder  {
        color: $color;
		opacity: $opacity;
		font-weight: 400;
		font-family: $force-mac-font-family;
		} // Safari and Chrome
		&::placeholder{
			color: $color;
			opacity: $opacity;
			font-weight: 400;
			font-family: $force-mac-font-family;
		}
}

// Placeholder text
@mixin selected($color, $background) {
  &::selection {
    background: $background; /* Safari */
    color: $color;
  }
  &::-moz-selection {
    background: $background; /* Safari */
    color: $color;
  }
}

@mixin placeholder-height($height) {
  &:-moz-placeholder {
    line-height: $height;
  }
  &::-webkit-input-placeholder {
    line-height: $height;
  }
  &.placeholder {
    line-height: $height;
  }
}

// Text overflow
// Requires inline-block or block for proper styling
@mixin text-overflow() {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

// CSS image replacement
@mixin text-hide() {
  font: 0/0 a;
  color: transparent;
  text-shadow: none;
  background-color: transparent;
  border: 0;
}

@mixin invisible() {
  visibility:hidden;
}

//Padding Helper Classes
@mixin generate-paddings-options-prefix($prefix,$n, $j: 0) {
  @if $j <= $n {
    @for $i from $j through $n {
      $step : $i*5;  
      .#{$prefix}-p-t-#{$step} {
        margin-top: ($step*1px) !important;
      }
      .#{$prefix}-p-r-#{$step} {
        margin-right: ($step*1px) !important;
      }
      .#{$prefix}-p-l-#{$step} {
        margin-left: ($step*1px) !important;
      }
      .#{$prefix}-p-b-#{$step} {
        margin-bottom: ($step*1px) !important;
      }
      //@include generate-margin-options($n, ($i + 1));
    }
  }
}
//Margins Helper Classes
@mixin generate-margin-options-prefix($prefix,$n, $j: 0) {
  @if $j <= $n {
    @for $i from $j through $n {
      $step : $i*5;  
      .#{$prefix}-m-t-#{$step} {
        margin-top: ($step*1px) !important;
      }
      .#{$prefix}-m-r-#{$step} {
        margin-right: ($step*1px) !important;
      }
      .#{$prefix}-m-l-#{$step} {
        margin-left: ($step*1px) !important;
      }
      .#{$prefix}-m-b-#{$step} {
        margin-bottom: ($step*1px) !important;
      }
      //@include generate-margin-options($n, ($i + 1));
    }
  }
}
