.react-slidedown.my-dropdown-slidedown {
  transition-duration: 0.3s;
}

.sidebar-header-icon {
  float: right;
}

.page-sidebar-corporate {
  background-color: #323237;
  z-index: 700;
  transition: transform 400ms cubic-bezier(0.05, 0.74, 0.27, 0.99);
}

.page-sidebar-corporate .sidebar-header {
  background-color: #323237;
  border-bottom: transparent;
  font-size: 24px;
  font-weight: 600;
}

.page-sidebar-corporate .sidebar-menu-corporate{
  height: calc(100% - 50px);
}

.page-sidebar-corporate .sidebar-menu .menu-items li:hover > a,
.page-sidebar-corporate .sidebar-menu .menu-items li:active > a,
.page-sidebar-corporate .sidebar-menu .menu-items li:hover > .icon-thumbnail,
.page-sidebar-corporate .sidebar-menu .menu-items li.open > .icon-thumbnail,
.page-sidebar-corporate .sidebar-menu .menu-items li.active > .icon-thumbnail {
  color: #fff;
}

.page-sidebar-corporate .sidebar-menu .menu-items li > a,
.page-sidebar-corporate .sidebar-menu .menu-items li > a,
.page-sidebar-corporate .sidebar-menu .menu-items li > .icon-thumbnail,
.page-sidebar-corporate .sidebar-menu .menu-items li > .icon-thumbnail,
.page-sidebar-corporate .sidebar-menu .menu-items li > .icon-thumbnail {
  color: #9e9ea6;
}
.page-sidebar-corporate .sidebar-menu .menu-items > li ul.sub-menu-corporate,
.page-sidebar-corporate
  .sidebar-menu
  .menu-items
  > li
  ul.sub-menu-corporate
  > li
  .icon-thumbnail {
  background-color: #27272a !important;
}
.page-sidebar-corporate .sidebar-menu .menu-items li.open > a,
.page-sidebar .sidebar-menu-corporate .menu-items li.active > a {
  color: #fff;
}

.page-sidebar-corporate
  .sidebar-menu
  .menu-items
  > li
  ul.sub-menu-corporate
  > li:hover
  > .icon-thumbnail {
  color: #fff
  }

  @media (max-width: 991px) {
    .page-sidebar-corporate .sidebar-menu-corporate .search-link{
      background-color: #27272a;
      opacity: 100%;
      margin-top: -40px;
      z-index: 1000;
      display: block;
      height: 54px;
      width: 100%;
      padding-left:20px;
      padding-right:20px;
      position: fixed;
    }
  }
  