/*** Desktops & Laptops ***/
@media only screen and (min-width: 980px) {
  body {
    &.ie9.menu-pin {
      .page-sidebar {
        transform: none !important;
        -webkit-transform: none !important;
        -ms-transform: none !important;
      }
    }
    &.menu-pin {
      .header .brand {
        width: 245px;
      }
      .page-container {
        padding-left: 0;
        .page-content-wrapper {
          .content {
            padding-left: $layout-sidepanel-pin-width;
          }
          .footer {
            left: $layout-sidepanel-pin-width;
          }
        }
      }
      [data-toggle-pin="sidebar"] {
        & > i:before {
          content: "\e9a2";
        }
      }
      .page-sidebar {
        transform: translate(
          $layout-sidepanel-width - $layout-sidepanel-width-collapsed,
          0
        ) !important;
        -webkit-transform: translate(
          $layout-sidepanel-width - $layout-sidepanel-width-collapsed,
          0
        ) !important;
        -ms-transform: translate(
          $layout-sidepanel-width - $layout-sidepanel-width-collapsed,
          0
        ) !important;
        width: $layout-sidepanel-pin-width;
        .sidebar-header .sidebar-header-controls {
          transform: translateX(21px);
          -webkit-transform: translateX(21px);
        }
        .menu-items .icon-thumbnail {
          transform: translate3d(-14px, 0, 0);
          -webkit-transform: -webkit-translate3d(-14px, 0, 0);
        }
      }
    }
    &.menu-behind {
      .page-sidebar {
        z-index: $zIndex-navbar - 1;
      }
      .header .brand {
        width: 200px;
        text-align: left;
        padding-left: 20px;
      }
    }
    &.box-layout {
      background-color: $color-contrast-lowest;
      & > .container,
      & > .full-height > .container {
        height: 100%;
        padding: 0;
        background-color: $color-contrast-lowest;
      }
      .header {
        background-color: transparent;
        border: 0;
        padding: 0;
        & > .container {
          background-color: $color-contrast-lowest;
          border-bottom: 1px solid rgba(230, 230, 230, 0.7);
          padding: 0 20px 0 0;
          height: 100%;
        }
      }
      .page-sidebar {
        left: auto;
        transform: none !important;
        -webkit-transform: none !important;
      }
      .page-container .page-content-wrapper .footer {
        width: auto;
      }
    }
  }

  .header {
    .brand {
      position: relative;
    }
    .user-info-wrapper {
      .user-details {
        .user-name {
          font-size: 16px;
        }
        .user-other {
          font-size: 10px;
        }
      }
      .user-pic {
        position: relative;
        top: -6px;
      }
    }
  }

  .notification-panel {
    width: 400px;
  }
}
