/*** General tablets and phones ***/
@media (max-width: 991px) {
  .hidden-lg-down {
    display: none !important;
  }

  .page-container {
    padding-left: 0;
  }

  .d-flex-lg-down {
    display: flex;
  }
  .justify-between-lg-down {
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
    -ms-flex-pack: justify;
    justify-content: space-between;
  }

  body {
    &.sidebar-open {
      .page-container {
        @include translate3d(250px, 0, 0);
        overflow: hidden;
        position: fixed;
      }
      .push-on-sidebar-open {
        @include translate3d(250px, 0, 0);
        overflow: hidden;
      }
    }
    &.box-layout {
      & > .container {
        padding: 0;
        height: 100%;
      }
      .header {
        & > .container {
          padding: 0;
          .pull-right .sm-action-bar {
            right: 0;
          }
        }
      }
    }
    &.horizontal-app-menu {
      .header {
        & > .container {
          padding: 0;
        }
      }
      .secondary-sidebar {
        margin-left: 0;
      }
    }
  }
  .sm-action-bar {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 10;
    & > a {
      padding: 10px;
      display: inline-block;
    }
  }
  .pace .pace-activity {
    top: 60px;
  }
  .page-sidebar {
    width: 250px;
    z-index: auto;
    left: 0 !important;
    -webkit-transform: translate3d(0, 0px, 0px) !important;
    transform: translate3d(0, 0px, 0px) !important;
    .sidebar-menu .menu-items > li ul.sub-menu > li {
      padding: 0px 30px 0 36px;
    }
  }

  .page-container {
    padding-left: 0;
    // z-index: 100;
    position: relative;
    transition: transform 0.25s ease;
    -webkit-transition: -webkit-transform 0.25s ease;
    .page-content-wrapper .content {
      overflow-x: hidden;
      height: auto; // minimizes address bar on scroll
    }
  }

  .icon-thumbnail {
    margin-right: 24px;
  }
  .page-sidebar {
    display: none;
    z-index: auto;
    &.visible {
      display: block;
    }
    .page-sidebar-inner {
      z-index: 1;
      left: 0 !important;
      width: 260px;
      .sidebar-slide {
        .sidebar-menu {
          overflow: scroll;
          -webkit-overflow-scrolling: touch;
          top: 40px;
          bottom: 0;
          ul > li > a > .badge {
            display: inline-block;
          }
        }
      }
    }
  }
  .secondary-sidebar {
    float: none;
    height: auto;
    left: 50%;
    margin-left: -125px;
    padding: 20px;
    position: absolute;
    right: 0;
    top: 60px;
    z-index: 9999;
    display: none;
    border-radius: 10px;
    .btn-compose {
      display: none;
    }
  }
  .inner-content,
  .horizontal-app-menu .inner-content {
    margin: 0;
  }
  .breadcrumb {
    padding-left: 15px;
    padding-right: 15px;
  }
  .copyright {
    padding-left: 15px;
    padding-right: 15px;
  }

  // Pages Notification customizations
  body > .pgn-wrapper[data-position="top"] {
    top: $layout-mobile-header-height;
    left: 0;
  }
  body > .pgn-wrapper[data-position="bottom"] {
    left: 0;
  }

  body > .pgn-wrapper[data-position$="-left"] {
    left: 20px;
    right: auto;
  }

  .sm-table {
    display: table;
    width: 100%;
  }
  .user-profile-wrapper {
    position: absolute;
    right: 50px;
    top: -9px;
  }

  .container {
    width: 100%;
  }

  //Table Block
  .table-responsive-block {
    table,
    thead,
    tbody,
    th,
    td,
    tr {
      display: block;
    }
    thead tr {
      position: absolute;
      top: -9999px;
      left: -9999px;
    }
    tr {
      border: 1px solid rgba(230, 230, 230, 0.7);
      margin-bottom: 15px;
    }
    td {
      /* Behave  like a "row" */
      border: none;
      border-bottom: 1px solid #eee;
      position: relative;
      padding-left: 50%;
      white-space: normal;
      text-align: left;
      &:last-child {
        border-bottom: 0;
      }
      &:before {
        /* Now like a table header */
        position: absolute;
        /* Top/left values mimic padding */
        top: 6px;
        left: 6px;
        width: 45%;
        padding-right: 10px;
        white-space: nowrap;
        text-align: left;
        font-weight: bold;
      }
      &:before {
        content: attr(data-title);
      }
    }
  }
  .kill-pull-md-down {
    position: relative;
    display: block;
    width: inherit;
    .toggle-secondary-sidebar {
      display: inline-block;
      font-size: 18px;
    }
  }

  //set a min hieght for dropdown tabs
  .nav-tabs-header.nav-tabs-linetriangle {
    height: 61px;
  }

  .overlay {
    padding-left: 15px;
  }
}

/* Landscape view of all tablet devices */
