    -webkit-perspective: 1000;
  }
  .page-container {
    .page-content-wrapper {
      .content {
        padding-top: $layout-header-height;
      }
    }
  }
  body.box-layout > .container {
    width: 100%;
  }
  .inner-content {
    margin-left: 0;
  }
  .secondary-sidebar {
    float: none;
    height: auto;
    left: 50%;
    margin-left: -155px;
		padding: 20px;
		padding-left: 0;
    position: absolute;
    right: 0;
    top: $layout-header-height;
    z-index: 9999;
    display: none;
		border-radius: 10px;
		box-shadow: rgba(15, 15, 15, 0.05) 0px 0px 0px 1px,
		rgba(15, 15, 15, 0.1) 0px 3px 6px, rgba(15, 15, 15, 0.2) 0px 9px 24px;
    .btn-compose {
      display: none;
    }
	}
	//email icon
	.mark-email{
		margin-left: 25px;
	}
  .horizontal-app-menu .secondary-sidebar {
    display: inline-block;
    position: relative;
    top: inherit;
    left: inherit;
    right: inherit;
    padding: 20px 0;
    border-radius: 0;
    float: left;
    height: 100%;
  }
  .split-details {
    margin-left: 0;
  }
  .toggle-secondary-sidebar {
    font-size: 18px;
    display: block;
    font-size: 18px;
    left: 50%;
    margin-left: -36px;
    position: absolute;
    & ~ .brand {
      display: none !important;
    }
  }
