  .lock-box {
    align-items: flex-start;
    height: auto;
  }
  .login-box {
    padding-right: 15px;
    padding-left: 15px;
  }
  .visible-xs-inline-block {
    display: inline-block !important;
  }

  .nav-tabs-linetriangle {
    &.nav-cust {
      flex-direction: column;
    }
    li {
      a.active:after,
      a.active:before {
        display: none;
      }
    }
  }

  //View Port Heights
  .sm-vh-25 {
    height: 25vh;
  }
  .sm-vh-50 {
    height: 50vh;
  }
  .sm-vh-75 {
    height: 75vh;
  }
  .sm-vh-100 {
    height: 100vh;
  }

  .horizontal-app-menu {
    .page-container .page-content-wrapper .content > .container {
      padding-left: 0;
      padding-right: 0;
    }
  }

  //Summernote btn-group wrap
  .summernote-wrapper .btn-group {
    -ms-flex-flow: wrap;
    flex-flow: wrap;
  }
}

/*** Phones ***/
@media (max-width: 480px) {
  body {
    width: 100%;
    #overlay-search {
      font-size: 48px;
      height: 118px;
      line-height: 46px;
    }
  }
  .page-sidebar .sidebar-header {
    height: $layout-mobile-header-height;
    line-height: $layout-mobile-header-height;
  }
  .panel {
    .panel-heading {
      padding-left: 15px;
    }
    .panel-body {
      padding: 15px;
      padding-top: 0;
    }
  }
  .map-controls {
    left: 10px;
  }
  .register-container {
    height: auto !important;
  }
}

/*** Retina Display Images **/
@media only screen and (-webkit-min-device-pixel-ratio: 2),
  only screen and (min--moz-device-pixel-ratio: 2),
  only screen and (-o-min-device-pixel-ratio: 200/100),
  only screen and (min-device-pixel-ratio: 2) {
  .editor-icon {
    background-image: url("#{$base-img-url}/editor_tray_2x.png");
    background-size: 480px 40px;
  }
  .alert .close {
    background: url("#{$base-img-url}/icons/noti-cross-2x.png") no-repeat scroll
      0 0 transparent;
    background-position: -9px -10px;
    width: 10px;
    height: 9px;
    position: relative;
    opacity: 0.8;
    background-size: 114px 29px;
  }
}

.hidden {
  display: none !important;
}
.visible-sm,
.visible-xs {
  display: none;
}

@media (max-width: 767px) {
  .visible-xs {
    display: block;
  }
  .hidden-xs {
    display: none;
  }
}
